<template>
  <div class="layout">
    <Row>
      <Col>
        <NavBar :activeName="'education'"></NavBar>
      </Col>
    </Row>
    <Row>
      <Col class="w1200">
        <Layout>
          <Row>
            <Breadcrumb :style="{margin: '16px 0'}">
              <BreadcrumbItem to="/">首页</BreadcrumbItem>
              <BreadcrumbItem>教育经历</BreadcrumbItem>
            </Breadcrumb>
          </Row>
          <Row>
            <Content class="content">
              <Layout>
                <Content class="content-center">
                  <Row>
                    <Col></Col>
                  </Row>
                </Content>
              </Layout>
            </Content>
          </Row>
        </Layout>
      </Col>
    </Row>
    <Row>
      <Col>
        <Footer></Footer>
      </Col>
    </Row>
  </div>
</template>

<script>
import NavBar from '../components/NavBar.vue' // 引进菜单模板
import Footer from '../components/Footer.vue' // 引进底部模板
export default {
  name: 'Education',
  components: {
    NavBar, // 使用菜单组件
    Footer // 使用底部组件
  },
  data () {
    return {
      msg: 'Education'
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
  .layout {
    min-width: 800px;
    background: #f5f7f9;
    position: relative;
    border-radius: 4px;
    overflow: hidden;
  }
  .w1200 {
    margin: 0 auto;
    width: 1200px;
  }
  .content {
    margin-bottom: 50px;
    min-height: 530px;
    background: #fff;
  }
  .content-center {
    padding: 24px;
    background: #fff;
  }
</style>



